export default {
  // 时间不够十位数自动补0
  add0: function add0(m) {
    return m < 10 ? '0' + m : m;
  },
  // 中国标准时间format yyyy-mm-dd hh:mm:ss
  format: function format(time) {
    var date = new Date(time); //时间戳为10位需*1000，时间戳为13位的话不需乘1000

    var Y, M, D, h, m, s;
    Y = date.getFullYear();
    M = date.getMonth() + 1;
    D = date.getDate();
    h = date.getHours();
    m = date.getMinutes();
    s = date.getSeconds(); // this.nowTime = this.add0(Y) + '-' + this.add0(M) + '-' + this.add0(D) + ' ' + this.add0(h) + ':' + this.add0(m) + ':' + this.add0(s);

    return this.add0(Y) + '-' + this.add0(M) + '-' + this.add0(D) + ' ' + this.add0(h) + ':' + this.add0(m) + ':' + this.add0(s);
  },
  getTimeState: function getTimeState() {
    // 获取当前时间
    var timeNow = new Date(); // 获取当前小时

    var hours = timeNow.getHours(); // 设置默认文字

    var text = ""; // 判断当前时间段

    if (hours >= 0 && hours < 9) {
      text = "\u65E9\u4E0A\u597D";
    } else if (hours >= 9 && hours < 11) {
      text = "\u4E0A\u5348\u597D";
    } else if (hours >= 11 && hours < 13) {
      text = "\u4E2D\u5348\u597D";
    } else if (hours >= 13 && hours < 18) {
      text = "\u4E0B\u5348\u597D";
    } else if (hours >= 18 && hours < 24) {
      text = "\u665A\u4E0A\u597D";
    }

    console.log("hours >>>>>", hours);
    console.log("text >>>>", text); // 返回当前时间段对应的状态

    return text;
  }
};